/* eslint eqeqeq: "off", camelcase: "off" */

import './popups.scss'

import qs from 'qs'
import _ from 'lodash'
import axios from 'axios'

/* eslint-disable */
import {
  popup_open_assistant,
  popup_upgrade_assistant,
  popup_invalid_assistant
} from './popups'
/* eslint-enable */

let assistant = axios.create({
  baseURL: '/',
  timeout: 10000
})

assistant.interceptors.request
  .use((config) => {
    config.url = __ENV__.qk_endpoint + config.url
    if (config.method.toLowerCase() == 'post' && config.data) {
      config.data = qs.stringify(config.data)
    }
    return config
  }, (err) => {
    // TODO:

    return Promise.reject(err)
  })

assistant.interceptors.response
  .use((response) => {
    return response
  }, (err) => {
    // TODO:
    let res = err.response

    if (res && res.status === 404) {
      $toast.show('服务不可用')
    }

    return Promise.reject(err)
  })

// 检测助手
const alive = () => {
  return assistant.get('keyServices/assistant.getInfo', {
    timeout: 1000
  })
}

// 分享内容
const share = (params) => {
  let scheme = __ENV__.qk_scheme
  let host = 'share'
  let query = ''
  _.each(params, (v, k) => {
    if (!query) {
      query = k + '=' + encodeURIComponent(v)
    } else {
      query += '&' + k + '=' + encodeURIComponent(v)
    }
  })

  // console.log(`${scheme}${host}?${query}`)
  location.href = `${scheme}${host}?${query}`
}

// 轮询检测助手是否存在
const POLLING_DELAY = 1000
let queue = []

const ping = () => {
  queue.splice(0, 1)

  assistant
    .get('keyServices/assistant.getInfo')
    .then(res => res.data && res.data.payload)
    .then((payload) => {
      let scheme_qk = payload.scheme_qk
      if (scheme_qk) {
        $storage.set('qk:app:scheme', scheme_qk)
      }

      console.info('检测到助手')
      location.reload()
    })
    .catch(e => {
      queue.push(setTimeout(ping, POLLING_DELAY))
    })
}

const poll = () => {
  if (queue.length > 0) return
  queue.push(0)
  ping()
}

const cancelPoll = () => {
  while (queue.length > 0) {
    clearTimeout(queue.pop())
  }
}

// 向服务端上报助手状态（心跳）
const updateState = () => {
  return assistant
    .get('keyServices/updateState.once')
    .catch(e => console.log(e))
}

// 开启推送
const isPushEnabled = () => {
  return assistant
    .get('/keyServices/notification.enabled')
    .catch(e => console.log(e))
}

// 检测应用
const getAppInfo = (bundle_id) => {
  return assistant
    .get('keyServices/app.getInfo?bundle_id=' + bundle_id)
    .catch(e => console.log(e))
}

// 打开应用
// @param params.bid 应用Bundle ID
// @param params.exe 应用运行状态
const openApp = (params) => {
  return assistant
    .get('keyServices/open.app', {
      params: params
    })
    .catch(e => console.log(e))
}

 // 打开应用iOS11
 // @param params.bid 应用Bundle ID
 // @param params.url 回调地址
const openAppIOS11 = (params) => {
  return assistant
    .get('keyServices/open.app.11', {

      params: params
    })
    .catch(e => console.log(e))
}

// kill me
const kill = () => {
  return assistant.get('keyServices/kill.me')
}

const makePopup = (type, key_msg) => {
  return new Promise((resolve) => {
    let popup

    switch (type) {
      case 'upgrade':
        popup = $popup.fromTemplate(
          popup_upgrade_assistant.template,
          popup_upgrade_assistant.options
        )
        resolve(popup)
        break
      case 'invalid':
        popup = $popup.fromTemplate(
          popup_invalid_assistant.template,
          popup_invalid_assistant.options
        )
        resolve(popup)
        break
      default:
        return axios
          .get('/s4/assistant.info')
          .then(res => res.data && res.data.payload)
          .then((payload) => {
            let download_url = payload.download_url
            let scheme = payload.app_scheme
            let app_status = payload.app_status

            let tpl = app_status == 1
                ? popup_open_assistant.template(download_url)
                : popup_invalid_assistant.template

            let options = app_status == 1
                ? popup_open_assistant.options(scheme)
                : popup_invalid_assistant.makeOptions(download_url)

            let popup = $popup.fromTemplate(tpl, options)
            resolve(popup)
            poll()
          })
    }
  })
}

// Deprecated
const showOpenAssistantPopup = () => {
  axios
    .get('/s4/assistant.info')
    .then(res => res.data && res.data.payload)
    .then((payload) => {
      let download_url = payload.download_url
      let scheme = payload.app_scheme
      let popup = $popup.fromTemplate(
        popup_open_assistant.template(download_url),
        popup_open_assistant.options(scheme)
      )

      popup.show()
    })
}

_.extend(assistant, {
  alive,
  poll,
  cancelPoll,
  updateState,
  getAppInfo,
  openApp,
  openAppIOS11,
  kill,
  isPushEnabled,
  makePopup,
  share,

  // Deprecated
  showOpenAssistantPopup
})

export default assistant



