<template>
  <CustomPopup ref="p">
    <div class="popup-body install-entrance-popup">
      <div class="step-1" v-if="step == 1">
        <div class="title">安装钱咖</div>
        <div class="desc">证书形式快捷安装到桌面</div>
        <div class="desc-pic"><img src="//qianka.b0.upaiyun.com/images/9fef37510ec4538c135335dead044641.png" alt=""></div>
        <button class="button button-royal button-small button-clear button-close" @click="hide(-1)">
          <i class="ion-ios-close-empty"></i>
        </button>

        <button class="button button-assertive btn-install" @click="install()">立即安装</button>
      </div>
      <div class="step-2" v-if="step == 2">
        <div>回到桌面打开钱咖</div>
        <div><img src="//qianka.b0.upaiyun.com/images/6fc1f2a7ca46a6e173f349999198f43e.png" alt=""></div>
        <button class="button button-royal button-small button-clear button-close" @click="know(-1)">
          <i class="ion-ios-close-empty"></i>
        </button>

        <button class="button button-assertive btn-install" @click="know()">知道了</button>
      </div>
    </div>
  </CustomPopup>
</template>
<style lang='scss' scoped>
  @import '../../scss/mixins';

  .install-entrance-popup {
    text-align: center;

    .step-1 {
      .title {
        font-size: 14px;
        color: #3A3A3A;
        margin-top: 12px;
        margin-bottom: 2px;

        @include font-regular;
      }

      .desc {
        font-size: 12px;
        color: #B5B5B5;
        margin-bottom: 17px;

        @include font-regular;
      }

      .desc-pic {
        margin-bottom: 32px;

        img {
          height: 178px;
        }
      }

      .btn-install {
        width: 160px;
        height: 40px;
        min-height: 40px;
        line-height: 40px;
        background: #EF4F2A;
        border-radius: 2px;
        font-size: 13px;
        color: #FFFFFF;

        @include font-medium();
      }
    }

    .step-2 {
      div:first-child {
        font-size: 16px;
        color: #484746;
        margin-bottom: 30px;
      }

      div:nth-child(2) {
        margin-bottom: 32px;

        img {
          height: 166px;
        }
      }

      .btn-install {
        width: 160px;
        height: 40px;
        min-height: 40px;
        line-height: 40px;
        background: #EF4F2A;
        border-radius: 2px;
        font-size: 13px;
        color: #FFFFFF;

        @include font-medium();
      }
    }
  }
</style>
<script>
  import CustomPopup from '@/widget/CustomPopup'

  export default {
    components: {
      CustomPopup
    },

    data () {
      return {
        step: 1
      }
    },

    methods: {
      show () {
        this.$refs.p.show()
      },

      hide (buttonIndex) {
        this.$refs.p.hide(buttonIndex)
      },

      install () {
        $bus.$emit('installEntrance')
        setTimeout(() => {
          this.step = 2
        }, 1000)
      },

      know () {
        this.hide(-1)
        this.step = 1
      }
    }
  }
</script>



