<template>
  <div :class="{'page': !isPad, 'page-ipad': isPad}" v-nav="{hideNavbar: true}">
    <div class="page-content" style="background-color: #FFF;" v-if="!isPad">
      <WelcomeContent />
    </div>

    <WelcomeContent v-if="isPad"/>
    <InstallEntrancePopup ref="installEntrancePopup" />
  </div>
</template>

<style lang="scss">
  .page-ipad {
    background-color: #FFF;
    position: absolute;
    width: 100%;
    height: 100%;
  }
</style>

<script>
  import WelcomeContent from './WelcomeContent.vue'
  import InstallEntrancePopup from './InstallEntrancePopup.vue'

  export default {
    components: {
      WelcomeContent,
      InstallEntrancePopup
    },

    data () {
      return {
        isPad: /iPad/.test(navigator.userAgent)
      }
    },

    mounted () {
      $bus.$on('showInstallEntrancePopup', () => {
        this.$refs.installEntrancePopup.show()
      })
    }
  }
</script>



