<template>
  <div class='qk-index-wrapper'>
    <transition-group name='animate-box' class='animate-box' tag='div'>
      <scalable class='user-act' width='197' height='48' v-for='c in cards' :key='c'>
        <img class='icon' :src='c.avatar' alt=''>
        <div class='nickname' v-text='c.nickname'></div>
        <div class='detail' v-html='c.detail'></div>
        <span class='time' v-text='c.time'></span>
      </scalable>
    </transition-group>

    <div class='gold'></div>
    <div class='gold-frame'></div>
    <div class='button-wrapper'>
      <button v-if='anonymous && !webclip' class='button button-assertive btn-start' @click='signIn()'>
        开始赚钱
      </button>
      <div v-if='anonymous && !webclip' class='link old-driver' @click='switchAccount()'>
        老用户登录
      </div>

      <button v-if='!anonymous && !webclip' class='button button-assertive btn-login' @click='signIn()'>
        重新登录
      </button>
      <div v-if='!anonymous && !webclip' class='link switch-account' @click='switchAccount()'>
        切换账号
      </div>
      <button v-if='webclip' class='button button-assertive btn-start' @click='installEntrance()'>
        安装钱咖
      </button>
    </div>
  </div>
</template>
<style lang='scss'>
  @import '../../scss/mixins';

  .qk-index-wrapper {
    height: 100%;

    @include background-image(url(//qianka.b0.upaiyun.com/images/f6c42914fd4ded2dca4b494d80d99a3a.png), 100%, 0px 0px);

    .animate-box {
      z-index: 0;
      position: absolute;
      top: 199px;
      left: 68px;
      width: 239px;
      height: 290px;
      padding: 21px 21px 0 21px;

      .user-act {
        background: rgba(255, 255, 255, 0.9);
        box-shadow: 0 2px 6px 0 rgba(253, 207, 89, 0.7);
        border-radius: 4px;
        margin-bottom: 10px;
        color: #484746;

        &:first-child {
          opacity: 0.44;
          transition: all 0.5s ease-out;
        }

        .icon {
          position: absolute;
          left: 12px;
          top: 20px;
          width: 60px;
          height: 60px;
          border-radius: 30px;
        }

        .nickname, .detail {
          font-size: 22px;
          line-height: 32px;
        }

        .nickname {
          height: 46px;
          padding: 14px 0 0 86px;
          margin-bottom: 2px;
        }

        .detail {
          padding: 0 0 0 86px;

          span.assertive {
            font-size: 26px;
            color: #ff6c5b;

            @include font-hzgb();
          }
        }

        .time {
          position: absolute;
          top: 20px;
          font-size: 18px;
          right: 20px;
          color: #c4c4c4;
        }
      }
    }

    .animate-box-enter {
      opacity: 0;
    }

    .animate-box-enter-active {
      transition: all 0.5s ease-out;
    }

    .animate-box-leave-active {
      animation: leave-out 1s;
    }

    @keyframes leave-out {
      0% {
        opacity: 0.44;
        height: 48px;
        margin-bottom: 10px;
      }

      50% {
        opacity: 0;
        height: 48px;
        margin-bottom: 10px;
      }

      100% {
        opacity: 0;
        height: 0;
        margin-bottom: 0;
      }
    }

    .gold {
      z-index: 2;
      position: absolute;
      top: 358px;
      left: 69px;
      width: 237px;
      height: 74px;

      @include background-image(url(//qianka.b0.upaiyun.com/images/38a1c1d093de86ccf588c8193e7f546e.png));
    }

    .gold-frame {
      z-index: 2;
      position: absolute;
      top: 199px;
      left: 68px;
      width: 239px;
      height: 290px;

      @include background-image(url(//qianka.b0.upaiyun.com/images/d6f8320a8de422f13835676589ff3a70.png));
    }

    .button-wrapper {
      position: absolute;
      top: 404px;
      left: 0;
      z-index: 3;
      width: 100%;
      height: 156px;

      @include background-image(url(//qianka.b0.upaiyun.com/images/7489e506cef8a2b213f6b127b5cc55cb.png), 100%, 0px 0px);

      .btn-start, .btn-login {
        position: absolute;
        left: 50%;
        top: 46px;
        margin-left: -100px;
        width: 200px;
        height: 44px;
        border-radius: 100px;
        font-size: 14px;
        background: #ff6c5b;
      }

      .link {
        position: absolute;
        left: 50%;
        top: 110px;
        font-size: 14px;
        line-height: 14px;
        height: 20px;
        padding: 3px 0;
        color: $assertive;
        border-bottom: 1px solid $assertive;
        text-align: center;

        &.old-driver {
          color: #ff6c5b;
          width: 72px;
          margin-left: -36px;
          border-bottom: 1px solid #ff6c5b;
        }

        &.switch-account {
          color: #ff6c5b;
          width: 58px;
          margin-left: -29px;
          border-bottom: 1px solid #ff6c5b;
        }
      }
    }
  }
</style>
<script>
  import {
    COOKIE_KEY_LAST_UID
  } from 'constants'

  const isPad = /iPad/.test(navigator.userAgent)

  export default {
    props: {
      onInstallClick: Function
    },

    data () {
      return {
        anonymous: !$cookie.get(COOKIE_KEY_LAST_UID),
        cards: [],
        webclip: ''
      }
    },

    mounted () {
      $bus.$on('installEntrance', () => {
        location.href = this.webclip
      })

      $api.welcome(2)
        .then(res => res.data && res.data.payload)
        .then(payload => {
          this.webclip = payload.webclip
          this.roll(payload.task_data)
        })
      this.roll(this.cards)
    },

    beforeDestroy () {
      clearInterval(this.rollInterval)
    },

    methods: {
      roll (cards) {
        var pos = 0
        while (pos < cards.length && pos < 3) {
          this.cards.push(cards[pos++])
        }

        if (pos < 2) {
          return
        }
        this.rollInterval = setInterval(() => {
          if (pos >= cards.length) {
            pos = 0
          }
          this.cards.shift()
          setTimeout(() => {
            this.cards.push(cards[pos++])
          }, 1000)
        }, 3000)
      },

      signIn () {
        $api
          .createUser()
          .then(() => {
            isPad
              ? location.href = '/v4/dashboard'
              : $router.forward('/dashboard')
          })
      },

      // 切换账号、老用户登录
      switchAccount () {
        isPad
          ? location.href = '/v4/login'
          : $router.forward('/login')
      },

      installEntrance () {
        $bus.$emit('showInstallEntrancePopup')
      }
    }
  }
</script>



