<template>
  <transition name="fade-webclip">
    <div class="webclip" v-if="show">
      <div class="qk-icon"></div>
      <div class="title">在桌面安装钱咖快捷入口</div>
      <div class="desc">随时随地来赚钱</div>
      <button class="button button-assertive button-small btn-install" @click="onClick()">安装</button>
    </div>
  </transition>
</template>
<style lang="scss" scoped>
  @import "../scss/mixins";

  .fade-webclip-transition {
    transition: all .3s ease;
    overflow: hidden;
    opacity: 1;
    transform: translate3d(0,0,0);
    -webkit-transform: translate3d(0,0,0);
  }

  .fade-webclip-enter, .fade-webclip-leave-to, .fade-webclip-leave {
    opacity: 0;
    transform: translate3d(0,100%,0);
    -webkit-transform: translate3d(0,100%,0);
  }

  .fade-webclip-enter-active {
    opacity: 1;
    transition: all .3s ease;
  }

  .webclip {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 60px;
    // background-image: linear-gradient(-180deg, #2D2C33 0%, rgba(24,23,26,0.85) 98%);
    // background-color: #000;
    // opacity: 0.8;
    background: rgba(0,0,0,.8);

    color: #FFF;
    width: 100%;
    padding: 10px 16px 10px 16px+40px+12px;
    z-index: 20;

    .qk-icon {
      width: 40px;
      height: 40px;
      border-radius: 4px;
      @include background-image(url(//qianka.b0.upaiyun.com/images/4ee0c9242575135ba0c1613e492ce0a6.png));
      position: absolute;
      top: 10px;
      left: 16px;
    }

    .title {
      font-size: 14px;
      line-height: 20px;
    }

    .desc {
      font-size: 12px;
      line-height: 17px;
      opacity: 0.5;
      @include font-regular;
    }

    .btn-install {
      position: absolute;
      padding: 0;
      width: 56px;
      height: 28px;
      min-height: 28px;
      line-height: 28px;
      top: 16px;
      right: 16px;
      border-radius: 2px;
    }
  }
</style>
<script>
  export default {
    created () {
      $bus.$on('showWebclip', (data) => {
        this.url = data.url
        setTimeout(() => {
          this.show = true
        }, 500)
      })

      $bus.$on('hideWebclip', () => {
        this.show = false
      })
    },

    data () {
      return {
        url: '',
        show: false
      }
    },

    methods: {
      onClick () {
        console.log('install webclip!')
        location.href = this.url
        this.show = false
      }
    }
  }
</script>



