          <div class="title">
            ${title}
            <div class="tag"><img src="${tag.img}">${text}</div>
          </div>
          <div class="desc">${desc}</div>
        </div>
      `
    }

    return `
      <div class="qk-entrance">
        <img src="${icon}" class="icon">
        <div class="title">${title}</div>
        <div class="desc">${desc}</div>
      </div>
    `
  }

  const icon_timed = '//qianka.b0.upaiyun.com/images/36f067f14e0b97daaa0a8e761b097320.jpeg'
  const icon_loan = '//assets.qkcdn.com/images/aa6b56c81296aa279fa56fefde155934.png'
