  // tag
  const timed_tag = '//assets.qkcdn.com/images/83f8876166489abfea2c1b3c23de0602.png'
  const fanli_tag = '//assets.qkcdn.com/images/97f47020a8c7426c4527131c6dc0de95.png'
  const loan_tag = '//assets.qkcdn.com/images/a71d9a874def1e5966255f55cd4da121.png'
  const invite_tag = '//assets.qkcdn.com/images/f5124a12d65264fa292b30fd6a7043e8.png'
  const gaoe_tag = '//qianka.b0.upaiyun.com/images/ed0bd07f9cb261053c8b58a013966876.png'
  // const youlian_tag = '//qianka.b0.upaiyun.com/images/bac6c4010a1007aa4b62667753be7bb3.png'

  const top_entrances = [
    tplEntrance(icon_timed, '试玩赚钱', '下载应用领奖励', { img: timed_tag }),
    tplEntrance(icon_invite, '收徒赚钱', '上不封顶月入万', { img: invite_tag }),
    tplEntrance(icon_fanli, '淘宝返利', '买就返 超省钱', { img: fanli_tag }),
    tplEntrance(icon_loan, '快速借钱', '线上申请即放款', { img: loan_tag })
  ]

  const middle_entrances = [
    tplEntrance(icon_credit, '办卡赚钱', '办信用卡拿现金'),
    tplEntrance(icon_gaoe, '注册赚钱', '金融注册奖励高'),
    tplEntrance(icon_youlian, '开店赚钱', '月入2000不是梦'),
    tplEntrance(icon_lottery, '每日开奖', '千元大奖一起分')
