  // 测试入口，先注释
  // const entrancesForNewbie = [
  //   tplEntrance(icon_timed, '试玩赚钱', '下载应用领奖励'),
  //   tplEntrance(icon_invite, '收徒赚钱', '上不封顶月入万', { img: invite_tag }),
  //   tplEntrance(icon_youlian, '开店赚钱', '月赚2000不是梦'),
  //   tplEntrance(icon_lottery, '每日开奖', '千元大奖一起分')
  // ]

  export default {
    components: {
      AnimateNum
    },
    data () {
      return {
        // 用户信息
        avatar: 'http://assets.qkcdn.com/images/d750995328248d4a2f596ff283c359d8.png!200x200', // 默认头像
        balance: '0.00',
        total_reward: '0.00',
        today_reward: '0.00',
        withdraw_amount: '',
        jfb_convert_num: '',

        daily_task_quota: 0,
        animate_num_start: null,
        animate_num_end: null,
        weishang_url: '',

        // 入口
        top_entrances: top_entrances,
        middle_entrances: middle_entrances,

        jfb_is_open: 0,

        loaded: false,

        bind_mobile: 0,
        has_swiper: 0,
        banners: [],

        webclip: 0,

        gift: 0,
        // Hongbao vm
        hongbao: undefined,

        create_day: 20160101,
        bind_udid: 0,

        // 是否显示绑定手机的 tip
        showUnbindMobileTips: 0,
        unbindMobileTipsSlideIn: false,

        // 每日开奖（下注）
        lottery: {
          status: 0, // 每日开奖入口: 0 不展示入口; 1 展示入口及 new 标签; 2 展示入口但不展示 new 标签
          win: 0, // 是否中奖: 0 未中奖; 1 中奖
          win_info: { // 中奖具体信息
            lottery_id: undefined, // 中奖期数
            reward: '', // 中奖金额，单位：元
            bets: 0 // 中奖注数
          }
        },
        jfb_account: {},
        show_convert: 0,
        install_essential_type: '',
        second_hand_url: '',

        bilu_game: {
          show_bilu: 0,
          bilu_url: ''
        },

        national_day_flag: 0 // 国庆活动入口
      }
    },

    mounted () {
      channel.$on('updateIncome', (data) => {
        this.updateIncome(data)
      })
      $api.dashboard()
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          // 应用中心根据值随机展示入口
          this.install_essential_type = payload.install_essential_type
          // 二手回收展示入口
          this.second_hand_url = payload.second_hand_url

          this.bilu_game = payload.bilu_game

          this.national_day_flag = payload.national_day_flag

          if (payload.is_lite == 1) {
            tasksPath = '/tasks/lite?autopopup=first'
          }

          _.extend(this.$data, payload)

          let jfb_account = payload.jfb_account
          if (jfb_account && jfb_account.is_open) {
            this.total_reward = (parseFloat(payload.total_reward) + parseFloat(payload.jfb_account.total_to_price)).toFixed(2)
            this.today_reward = (parseFloat(payload.today_reward) + parseFloat(payload.jfb_account.today_to_price)).toFixed(2)
            this.balance = (parseFloat(payload.balance) + parseFloat(payload.jfb_account.surplus_to_price)).toFixed(2)
          }

          this.startAnimateNumber(this.balance, 300) // animate counter for the reward

          // 集分宝兑换数量展示逻辑 优先级低于提现
          if (this.withdraw_amount > 0) {
            this.jfb_account.convert = ''
          }
          if (this.withdraw_amount > 0 || this.jfb_account.convert > 0) {
            this.show_convert = 1
          }

          this.showFanliLink = jfb_account && jfb_account.is_open

          this.lottery = payload.lottery

          if (payload.show_customer) {
            $bus.$emit('showCustomerService')
          }

          this.showEntrancesAndLinks = true

          // 创建日期在 0728 到 0731 之间的新用户，隐藏信用卡和高额入口，先屏蔽，产品说还有可能再测试！！！还有一部分在GetChips
          // let create_day = parseInt(this.create_day)
          // if (create_day >= 20170728 && create_day <= 20170802) {
          //   this.entrances = entrancesForNewbie
          // } else {
          //   this.setGaoeNewTag(payload.hitasks && payload.hitasks.new)
          // }
          // 此处结束，测试完拿掉以上逻辑

          this.setGaoeNewTag(payload.hitasks && payload.hitasks.new)

          this.showUnbindMobileTips = !this.bind_mobile && this.total_reward >= 2
          if (this.showUnbindMobileTips) {
            setTimeout(() => { this.unbindMobileTipsSlideIn = true }, 750)
          }

          if (this.webclip) {
            $bus.$emit('showWebclip', { url: this.webclip })
          }

          if (this.banners.length) {
            this.has_swiper = 1
          }

          // 保存到 local storage
          $storage.set(KEY_UID, payload.uid)
          $storage.set(KEY_NICKNAME, payload.nickname)

          // 如果用户 total_reward 超过 2 元, 则需要在列表页面中展示类似客官常来啊之类的文字提示 (TaskListTopTips)
          let topTipsKey = `${KEY_TASK_LIST_TOP_TIPS}:${payload.uid}`
          if (parseFloat(payload.total_reward) >= 2 && !$storage.get(topTipsKey)) {
            $storage.set(topTipsKey, 1) // 1: 需要展示，未展示 2: 需要展示，已展示
          }

          // 新人红包
          if (this.gift) {
            // gift=4 特殊渠道新用户，不弹红包弹层，直接跳转列表页（后台设置红包金额为-1
            if (this.gift == 4) {
              $router.forward({ path: tasksPath })
            } else {
              this.prepareHongbao().then((hongbao) => {
                hongbao.show()
              })
            }
          }

          // 是否展示借钱周转
          this.show_borrow = payload.show_borrow

          // 开奖弹层
          if (this.lottery.win === 1 && this.lottery.win_info) {
            let popup = $popup.fromTemplate(
              popup_lottery_win.template(this.lottery.win_info),
              {
                effect: 'scale',
                cssClass: 'popup-lotterty-win',
                keep: true,
                buttons: [
                  {
                    text: '点击领取',
                    onClick: () => {
                      popup.updateButtonStatusByIndex(0, false)
                      $api.lotteryGetReward(this.lottery.win_info.lottery_id)
                        .then(res => {
                          let payload = res.data.payload
                          let error_code = res.data.err_code
                          let err_msg = res.data.err_msg
                          // 40002: 已领取  40003: 未中奖
                          if (error_code && error_code === 40002 || error_code === 40003) {
                            $toast.show(err_msg)
                            popup.hide()
                            return
                          } else {
                            popup.updateButtonStatusByIndex(0, true)
                            popup.hide()
                            $toast.show(`成功领取 ${payload.reward} 元奖励`)

                            let balance = parseFloat(this.balance) + parseFloat(payload.reward)
                            let total_income = parseFloat(this.total_reward) + parseFloat(payload.reward)
                            let today_income = parseFloat(this.today_reward) + parseFloat(payload.reward)

                            this.updateIncome({
                              balance: balance.toFixed(2),
                              total_income: total_income.toFixed(2),
                              today_income: today_income.toFixed(2),
                              delay: 300
                            })
                          }
                        },
                        (err) => {
                          console.log(err)
                          popup.updateButtonStatusByIndex(0, true)
                        })
                    }
                  }
                ]
              }
            )
            popup.show()
          }

          // 新版钱咖上线重要通知，4种样式
          if (!this.gift) {
            // 若从未显示过, 则进入显示逻辑
            var is_old_driver = parseInt(this.create_day) < 20170301

            const makeImportantPopup = (templateAndOptions) => {
              return $popup.fromTemplate(
                templateAndOptions.template,
                templateAndOptions.options
              )
            }

            if (this.bind_mobile && is_old_driver && !$storage.get(KEY_NEW_QIANKA_NOTICE + ':1')) { // 弹层一: 如果该用户为4.0上线前的老用户且绑定了手机，则第一次进入4.0首页会显示此弹层
              let popup = $popup.fromTemplate(
                popup_important_001.template(this.bind_mobile),
                popup_important_001.options
              )

              popup.show().then((buttonIndex) => {
                if (buttonIndex == 1) {
                  $router.forward({
                    path: '/user/bindMobile/1',
                    query: { backroute: '/dashboard' }
                  })
                }

                $storage.set(KEY_NEW_QIANKA_NOTICE + ':1', 1)
              })
            } else if (is_old_driver && !this.bind_mobile && !$storage.get(KEY_NEW_QIANKA_NOTICE + ':1')) { // 弹层二: 如果该用户为4.0上线前的老用户但未绑定手机，则第一次进入4.0首页会显示此弹层
              let popup = makeImportantPopup(popup_important_002)

              popup.show().then((buttonIndex) => {
                if (buttonIndex == 1) {
                  $router.forward({
                    path: '/user/bindMobile/0',
                    query: { backroute: '/dashboard' }
                  })
                }

                $storage.set(KEY_NEW_QIANKA_NOTICE + ':1', 1)
              })
            } else if (is_old_driver &&
              this.bind_mobile &&
              this.bind_udid &&
              $storage.get(FLASH_LOGIN_WITH_CERT)) { // 弹层三: 用户使用证书登录成功，并且该用户已绑定手机，每次成功登录后都会在首页显示此弹层
              let popup = makeImportantPopup(popup_important_003)

              popup.show()

              $storage.remove(FLASH_LOGIN_WITH_CERT)
            } else if (is_old_driver &&
              !this.bind_mobile &&
              this.bind_udid &&
              $storage.get(FLASH_LOGIN_WITH_CERT)) { // 弹层四: 用户使用证书登录成功，并且该用户未绑定手机，每次成功登录后都会在首页显示此弹层，弹层上没有关闭按钮
              let popup = makeImportantPopup(popup_important_004)

              popup.show().then((buttonIndex) => {
                if (buttonIndex == 0) {
                  $router.forward({
                    path: '/user/bindMobile/0',
                    query: { backroute: '/dashboard' }
                  })
                }
              })

              $storage.remove(FLASH_LOGIN_WITH_CERT)
            }
          }
        })
    },

    destroyed () {
      $bus.$emit('hideWebclip')
      $bus.$emit('hideCustomerService')
      if (this.hongbao) {
        this.hongbao.$destroy()
      }

      if (this.showAnimateNumDelay) {
        clearTimeout(this.showAnimateNumDelay)
      }
    },

    methods: {
      user () {
        $router.forward({
          path: '/user/'
        })
      },

      updateIncome (data) {
        this.balance = data.balance
        this.total_reward = data.total_income
        this.today_reward = data.today_income
        this.startAnimateNumber(data.balance, data.delay)
      },

      _taskList () {
        $router.forward(tasksPath)
      },

      topOnCellClick (index) {
        if (index == 0) {
          this._taskList()
        } else if (index == 1) {
          $router.forward('/apprentice')
        } else if (index == 2) {
          $router.forward('/rebate')
        } else if (index == 3) {
          $router.forward('/loan')
        }
      },

      middleOnCellClick (index) {
        if (index == 0) {
          $router.forward('/credit/list')
        } else if (index == 1) {
          $router.forward('/gaoe')
        } else if (index == 2) {
          location.href = this.weishang_url
        } else if (index == 3) {
          $router.forward('/lottery')
        }
      },

      jumpTo (target) {
        if (target === 'bilu') {
          window.open(this.bilu_game.bilu_url)
        } else if (target === 'fanli') {
          $router.forward('/rebate')
        } else if (target === 'kagou') {
          location.href = EXTERNAL_LINK_KAGOU + '?port=' + KEY_PORT
        } else if (target === 'hotapps') {
          $router.forward('/hotapps/boutique')
        } else if (target === 'recycle') {
          window.open(this.second_hand_url)
        }
      },

      goto_bind_mobile () {
        $router.forward({
          path: '/user/bindMobile/0',
          query: { backroute: '/dashboard' }
        })
      },

      startAnimateNumber (amount, delay = 0) {
        if ((parseInt(amount) != 0) && !amount) return
        this.animate_num_end = parseFloat(amount) || 0

        this.showAnimateNumDelay = setTimeout(() => {
          this.$refs.animatenum.run(this.animate_num_end)
        }, delay)
      },

      prepareHongbao () {
        let taskList = this._taskList
        return new Promise((resolve) => {
          require.ensure([], () => {
            let el = document.createElement('div')
            el.id = 'hongbao'
            document.body.appendChild(el)
            const Hongbao = require('./Hongbao.vue')

            let gift = this.gift
            let hongbao = new Vue({
              components: {
                Hongbao
              },
              el: '#hongbao',
              template: `
                <hongbao :gift="gift" ref="hongbao" :make-money-callback="onClick"></hongbao>
              `,
              data: {
                gift: gift
              },
              methods: {
                show () {
                  this.$refs.hongbao.show()
                },
                hide () {
                  this.$refs.hongbao.hide()
                },
                onClick: taskList
              }
            })

            resolve(hongbao)
          })
        })
      },

      setGaoeNewTag (ids) {
        let read = $storage.get(KEY_GAOE_TASK_READ)
        if (!read) return
        let unread_count = _.filter(ids, (id) => { return _.indexOf(read, id) == -1 }).length
        let img = ''
        if (unread_count > 0) {
          img = gaoe_tag
        }
        this.middle_entrances.splice(
          1,
          1,
          tplEntrance(
            icon_gaoe,
            '注册赚钱',
            '金融注册奖励高',
            {
              img: img,
              text: unread_count
            }
          )
        )
      },

      showNationalPopup () {
        let popup = $popup.fromTemplate(
          popup_national_event.template,
          popup_national_event.options
        )

        popup.show().then((buttonIndex) => {
          if (buttonIndex == 0) {
            $router.forward({
              path: '/apprentice'
            })
          }
        })
      }
    }
  }
</script>



