<!-- 新人专享 & 标准任务 -->
<template>
  <div>
    <!-- 新人专享 -->
    <ActionList v-if="newbie.length > 0" title="新人专享" :subtitle="newbieTips">
      <Action v-for="task in newbie" :key="task.task_id" @click.native="start(task)">
        <!-- 任务图标 -->
        <div class="icon" slot="icon">
          <img :src="task.icon">
        </div>

        <!-- 任务标题 -->
        <span slot="app-name" v-text="task.title"></span>
        <!-- 任务标签 -->
        <Tags slot="desc" :tags="task.tags"></Tags>
        <!-- 任务奖励金额 -->
        <div slot="reward-wrap" class="reward-content">
          <span class="plus">+</span>
          <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
          <span class="yuan">元</span>
        </div>
      </Action>
    </ActionList>

    <!-- 标准任务 -->
    <ActionList v-if="tasks.length === 0 && !has_tasks" title="标准任务">
      <Action>
        <div class="icon" slot="icon">
          <img src="//qianka.b0.upaiyun.com/images/809c958dfd2f7f6f78da97f40322423a.png">        
        </div>
        <span slot="app-name">暂无任务</span>
        <span slot="desc">请关注任务预告</span>
      </Action>
    </ActionList>
    <ActionList v-if="tasks.length > 0" title="标准任务" :subtitle="tasksTips">
      <Action v-for="task in tasks" :key="task.task_id" @click.native="start(task)">
        <!-- 任务图标 -->
        <div class="icon" slot="icon">
          <img :src="task.icon">
        </div>

        <!-- 任务标题 -->
        <span slot="app-name" v-text="task.title"></span>
        <!-- 任务标签 -->
        <Tags slot="desc" :tags="task.tags"></Tags>
        <!-- 任务奖励金额 -->
        <div slot="reward-wrap" class="reward-content" v-if="!task.portalName">
          <span class="plus">+</span>
          <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
          <span class="yuan">元</span>
        </div>
        <div slot="reward-wrap" class="reward-content" v-if="task.portalName">
          <span class="plus">+</span>
          <span class="reward" v-text="task.portalName === 'shoutu' ? '10' : '2000'"></span>
          <span class="yuan" v-text="task.portalName === 'shoutu' ? '元' : '元/月'"></span>
        </div>
      </Action>
    </ActionList>
  </div>
</template>
<script>
  import {mapState, mapGetters, mapActions} from 'vuex'
  import {Action, ActionList} from '../components/actions'
  import Tags from '../components/actions/Tags.vue'
  import downloadAssistantPopupMixin from './downloadAssistantPopupMixin'
  import { popup_has_task_ongoing } from '../popups/popups.js'
  import { crius } from 'utils'

  export default {
    components: {
      Action,
      ActionList,
      Tags
    },

    computed: {
      ...mapState('task', [
        'newbie',
        'tasks',
        'ongoing',
        'has_tasks'
      ]),

      ...mapGetters('task', [
        'newbieTips',
        'tasksTips'
      ]),

      ...mapGetters('assistant', [
        'isNewbie'
      ])
    },

    mixins: [downloadAssistantPopupMixin],

    methods: {
      shoutu () {
        $router.forward({
          path: '/apprentice',
          query: {
            from_shoutu_portal: 1
          }
        })
      },

      start (t) {
        this.isAssistantAlive().then(() => {
          // 优先处理放置于投放中板块内的portal
          if (t.isPortal) {
            t.onPortalClick()
            return
          }

          if (t.status === 2) { // 点击了进行中的任务，尝试直接进入任务详情页面
            $router.forward({
              path: `/task/detail/${t.task_id}`
            })

            return
          }

          let ongoingTask = _.find(this.ongoing, t => {
            return t.type === 1
          })

          if (ongoingTask && ongoingTask.id !== t.id) {
            $popup.fromTemplate(
              popup_has_task_ongoing.template('标准'),
              popup_has_task_ongoing.options
            )
              .show()
              .then((buttonIndex) => {
                if (buttonIndex === 1) {
                  this.$store.dispatch('task/start', t)
                }
              })
          } else {
            /* 统计抢任务点击 */
            crius.sendAction('EVENT', 'click_subtask_start', {
              task_id: t.id
            })
            /* 统计抢任务点击 - END */

            this.$store.dispatch('task/start', t)
          }
        }, () => {
          if (this.isNewbie) {
            this.showDownloadAssistantPopup()
          }
        })
      },

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      })
    }
  }
</script>



